import React, { useEffect, useState } from 'react';
import classNames from 'classnames';
import { clientAPI } from '../store/clientApi';
import { Loader } from './Loader';
import { useAppDispatch, useAppSelector } from '../store/hooks';
import { setAuthor } from '../store/Reducers/AuthorSlice';

export const UserSelector: React.FC = () => {
  const { author } = useAppSelector(state => state.author);
  const { data: users, isLoading } = clientAPI.useFetchAllUsersQuery('');
  const [expanded, setExpanded] = useState(false);
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (!expanded) {
      return;
    }

    const handleDocumentClick = () => {
      setExpanded(false);
    };

    document.addEventListener('click', handleDocumentClick);

    // eslint-disable-next-line consistent-return
    return () => {
      document.removeEventListener('click', handleDocumentClick);
    };
  }, [expanded]);

  return (
    <div className={classNames('dropdown', { 'is-active': expanded })}>
      <div className="dropdown-trigger">
        <button
          type="button"
          className="button"
          aria-haspopup="true"
          aria-controls="dropdown-menu"
          onClick={() => setExpanded(current => !current)}
        >
          <span>
            {author?.name || 'Choose a user'}
          </span>

          <span className="icon is-small">
            <i className="fas fa-angle-down" aria-hidden="true" />
          </span>
        </button>
      </div>

      <div className="dropdown-menu" id="dropdown-menu" role="menu">
        <div className="dropdown-content">
          {isLoading
            ? <Loader />
            : users && users.map(user => (
              <a
                key={user.id}
                href={`#user-${user.id}`}
                onClick={() => dispatch(setAuthor(user))}
                className={classNames('dropdown-item', {
                  'is-active': user.id === author?.id,
                })}
              >
                {user.name}
              </a>
            ))}
        </div>
      </div>
    </div>
  );
};
